'use client';
import { useState } from 'react';
import styles from './page.module.css';

export default function UploadPage() {
  const [videoFile, setVideoFile] = useState<File | null>(null);
  const [isUploading, setIsUploading] = useState(false);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file && file.type.startsWith('video/')) {
      setVideoFile(file);
    } else {
      alert('Please upload a valid video file.');
    }
  };

  const handleUpload = async () => {
    if (!videoFile) return;
    setIsUploading(true);

    const formData = new FormData();
    formData.append('video', videoFile);

    try {
      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) throw new Error('Upload failed');

      const data = await response.json();
      console.log('‚úÖ Upload success:', data);

      // Simulate redirect
      window.location.href = '/summary';
    } catch (error) {
      console.error('üö® Upload error:', error);
      alert('Upload failed. Please try again.');
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.card}>
        <h1 className={styles.title}>Upload a Zoom Recording</h1>
        <p className={styles.subtitle}>Supported formats: .mp4, .mov, .mkv</p>

        <label htmlFor="video-upload" className={styles.fileInputLabel}>
          üìÅ Click to select a video file
          <input
            id="video-upload"
            type="file"
            accept="video/*"
            onChange={handleFileChange}
            style={{ display: 'none' }}
          />
        </label>

        {videoFile && (
          <p className={styles.fileName}>
            Selected: <strong>{videoFile.name}</strong>
          </p>
        )}

        <button
          onClick={handleUpload}
          disabled={!videoFile || isUploading}
          className={styles.button}
        >
          {isUploading ? 'Uploading...' : 'Upload & Start Analysis'}
        </button>
      </div>
    </div>
  );
}

